import React,{Component} from 'react'
import { Doughnut, Line, Bar, Bubble,Pie} from 'react-chartjs-2';
import Drivers from '../../components/Drivers'

export default class Pending extends React.Component{
    constructor (props){
        super(props)
        this.state = {
            data:[],
            driversArr: [],
            total : "0",
        }
    }

    componentDidMount(){
        this.getDrivers()
    }

    getDrivers(){
        if (this.Drivers.state.loading == false){
          this.setState({
          driversArr : this.Drivers.state.pending,
        })
        let tempArr =  new Array();
        
        tempArr.push(this.Drivers.state.pending[0].date.split(' ')[0])
        for (var x = 0; x < this.Drivers.state.pending.length; x++){
          let counter = true
            for (var i = 0; i < tempArr.length; i++){
                if (this.Drivers.state.pending[x].date != undefined){
                if (this.Drivers.state.pending[x].date.split(' ')[0] === tempArr[i]){
                  counter = false
                }
            }
             
            }
            if (counter && this.Drivers.state.pending[x].date != undefined)
                tempArr.push(this.Drivers.state.pending[x].date.split(' ')[0])
        }
        this.countDrivers(tempArr)
        setTimeout(() => {
          this.getDrivers();
        }, 60000);
        }else{
          setTimeout(() => {
            this.getDrivers();
          }, 1000);
        }
      }

      countDrivers(drivers){
        var tempArr =  new Array()
        var labels =  new Array()
        drivers.sort()
          for (var i = 0; i < drivers.length; i++){
              var total = 0;
              labels.push(drivers[i])
              for (var x = 0; x < this.Drivers.state.pending.length; x++){
                if (this.Drivers.state.pending[x].date != undefined){
                  if (drivers[i] === this.Drivers.state.pending[x].date.split(' ')[0]){
                    total++;
                  }
                }
              }
              tempArr.push(total)
          }
          this.setState({
            data:{
                labels: labels,
                datasets: [{
                  backgroundColor: [
                    'rgba(255, 99, 132, 1)',
                    'rgba(54, 162, 235, 1)',
                    'rgba(255, 206, 86, 1)',
                    'rgba(75, 192, 192, 1)',
                    'rgba(153, 102, 255, 1)',
                    'rgba(255, 159, 64, 1)'
                ],
                borderColor: [
                    'rgba(255, 99, 132, 1)',
                    'rgba(54, 162, 235, 1)',
                    'rgba(255, 206, 86, 1)',
                    'rgba(75, 192, 192, 1)',
                    'rgba(153, 102, 255, 1)',
                    'rgba(255, 159, 64, 1)'
                ],
                borderWidth: 1,
                    data: tempArr,
                }],
                options: { 
                    scales: {
                        yAxes: [{
                            stacked: true
                        }]
                    }
                }
            }
          })
      }

    render(){
        return(
                <div>
                    <div style={{display:'none'}}>
                        <Drivers ref={ref=>{this.Drivers = ref}} />
                    </div>
                    <Bar data={this.state.data} legend={false} />
                </div>
        )
    }
}